.grid-container {
    display: grid;
    grid-template-columns: 1fr 1fr 1fr 1fr;
    grid-template-rows: 1.3fr 0.7fr 0.7fr 0.7fr 1.4fr;
    grid-gap: 0px 0px;
    gap: 0px 0px;
    grid-template-areas:
      ". . . ."
      ". pregunta pregunta ."
      ". pregunta-1 pregunta-2 ."
      ". pregunta-3 pregunta-4 ."
      ". . . .";
  }
  
  .pregunta { grid-area: pregunta; }
  
  .pregunta-1 { grid-area: pregunta-1; }
  
  .pregunta-2 { grid-area: pregunta-2; }
  
  .pregunta-3 { grid-area: pregunta-3; }
  
  .pregunta-4 { grid-area: pregunta-4; }
  
  @keyframes move {
    100% {
        transform: translate3d(0, 0, 1px) rotate(360deg);
    }
}

